.about-section {
        width: 80vw;
        margin-top: 10vh;
        display: flex;
        justify-content: space-between;
        align-items: center;
}
.about-section .content {
    width: 40%;
    padding: .9em;
    line-height: 1.5;
}

.about-section .image-wrapper {
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}

.container{
    width: 90%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}


@media (max-width: 1200px){
    svg{
        width: 400px;
    }
}


@media (max-width: 980px){
    svg{
        width: 350px;
    }

    .content p{
        font-size: 14px;
    }
}

@media (max-width: 900px){
    svg{
        width: 330px;
    }

    .content p{
        font-size: 13px;
    }
}

@media (max-width: 900px){
    svg{
        width: 310px;
    }

    .content p{
        font-size: 12px;
    }

    .content h1{
        font-size: 30px;
    }
}


@media (max-width: 730px){
    svg{
        width: 280px;
    }

    .content p{
        font-size: 11.5px;
    }

    .content h1{
        font-size: 25px;
    }
}

@media (max-width: 660px){
    svg{
        width: 270px;
    }

    .content p{
        font-size: 11px;
    }

    .content h1{
        font-size: 23px;
    }
}

@media (max-width: 570px){
    svg{
        width: 260px;
    }

    .content p{
        font-size: 10px;
    }

    .content h1{
        font-size: 22px;
    }
}

@media (max-width: 550px){
    svg{
        /* display: none; */
        width: 180px;
    }
    .content p{
        font-size: 9px;
    }

    .content h1{
        font-size: 20px;
    }

    .about-section{
        margin-top: 7vh;
        width: 90%;
    }
}

@media (max-width: 455px){
    svg{
        /* display: none; */
        width: 160px;
    }
    .content p{
        font-size: 8px;
    }

    .content h1{
        font-size: 18px;
    }

    .about-section{
        margin-top: 20px;
    }
}

