
#home{
    width: 90%;
    margin: 0 auto;
	height: 80vh;
	place-items: center;
    margin-top: 10px;
    /* background-image: url(../../image/fileCover.png); */
    background-image: url("https://images.unsplash.com/photo-1635776062043-223faf322554?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1032&q=80");
    background-repeat: no-repeat;
    opacity: .9;
    background-size: cover;
    background-position: center;
}

.home-content{
  font-weight: bold;
  position: relative;
  top: 50%;
  left: 20%;
  transform: translate(-50%, -50%);
  z-index: 2;
  text-align: left;
  /* color: rgb(73, 103, 110); */
  color: rgb(33, 45, 48);
}

.home-content p {
    font-size: 20px;
}

.home-wrapper h1 {
    font-size: 50px;
}


/* .webs {
    position: relative;
    top: 50px;
} */

.home-links{
    position: relative;
    top: 20px;
}


.home-links ul{
    display: flex;
    align-items: left;

}

.home-links ul li{
    list-style-type: none;
    margin-right: 10px;
}

ul li a{
    cursor: pointer;
    color: rgb(33, 45, 48);
    /* color: rgb(73, 103, 110); */
}

@media screen and (max-width: 960px) {
    .home-text h1 {
        font-size: 40px;
    }
}

@media (max-width: 765px){
   

    .home-text h1 {
        font-size: 30px;
    }
    .home-text p {
        margin-top: 10px;
    }
}



@media (max-width: 500px){

    .home-content{
        text-align: center;
        position: relative;
        top: 50%;
        left: 50%;
    }
}


@media (max-width: 450px){

    .home-text h1 {
        font-size: 28px;
    }
}


@media (max-width: 395px){

    .home-text h1 {
        font-size: 26px;
    }
    .home-text p {
        font-size: 16px;
    }
}




